import React,{Component} from 'react';
import './post.css';
import ReplyForm from '../../reply-form/components/reply-form';
import Reply from '../../reply/components/reply';

// Icons/
import upvoteIMG from '../../icons/images/upvote.png';
import downvoteIMG from '../../icons/images/downvote.png';
import replyIMG from "../../icons/images/reply-icon.png";

class Post extends Component {
  state={
    reply: [],
    likes: 0,
    replyForm: false,
  }
  handleReplyForm = () => {
    this.setState({
      replyForm: !this.state.replyForm
    })
  }

  likeUp = () => {
    this.setState({ likes: this.state.likes + 1 })
  }

  likeDown = () => {
    this.setState({ likes: this.state.likes - 1 })

  }

  addReply = (newReplyBody) => {
    if (newReplyBody.trim() !== "") {
    const newState = Object.assign({}, this.state);
    newState.reply.push(newReplyBody);
    this.setState(newState);
    if (this.state.replyForm) {
      this.setState({
        replyForm: false
      })
    }
  }
  }  
  render(){
     return(
     <div className="post">
        <div className='post-container'>
          <div className="post-name">
            {this.props.name}
          </div>
          <div className="post-body">
            {this.props.postBody} 
          </div>
    <div className="post-icons" >
        <img id="upvote" onClick={this.likeUp} src={upvoteIMG} alt="Upvote" width="25"/>
          {this.state.likes}
        <img id="downvote" onClick={this.likeDown} src={downvoteIMG} alt="Downvote" width="25" />
        <img id="reply" onClick={this.handleReplyForm} alt="Reply" src={replyIMG} width="20" /> 
        <i onClick={this.handleReplyForm}>reply</i>
     </div>
       </div>
      <div>
     {
      this.state.reply.map(( replyBody, idx) => {
        return(
           <Reply
            name={this.props.name}
            key={idx}
            replyBody={replyBody}
            likeUp={this.likeUp}
            likeDown={this.likeDown}
            likes={this.state.likes}
            handleReplyForm={this.handleReplyForm}
           />
         )
     }) }
     </div>
     <div>
       <ReplyForm
             replyForm={this.state.replyForm}
             addReply={this.addReply}
       />
     </div>
    </div>
     )}
    }

export default Post;