import React, { Component } from 'react';
import './reply-form.css';

class ReplyForm extends Component{

  state = {
    newReplyBody: '',
    replyForm:false,
  }

  handleReplyFormInputChange = (event) => {
    this.setState({
      newReplyBody: event.target.value
    })
  }

  createReply = () => {
    this.props.addReply(this.state.newReplyBody);
    this.setState({
      newReplyBody: '',
    })

  }

  render(){
    return(
      <div>
        {
          this.props.replyForm && <div className="reply-form">
            <div className="">
              <textarea className="reply-input" value={this.state.newReplyBody} onChange={this.handleReplyFormInputChange} />
              <button className="reply-button" onClick={this.createReply}>Reply</button>
            </div>
          </div>
        }
        
      </div>
      
    )
  }
}

export default ReplyForm;