import React,{ Component } from 'react';
import Post from '../../post/components/post';
import PostEditor from '../../post-editor/components/post-editor';
import './container.css'

class ThreadDisplay extends Component{

  state = {
    posts: [],
    form:[],
    likes:0,
    replyForm:false,
    replyId:[],
    name:[],
  }
 
  addPost = (newPostBody, name, nameForm) => {
    if(nameForm===true){
      if (newPostBody.trim() !== "" && name.trim() !== "") {
        const newState = Object.assign({}, this.state);
        newState.posts.push(newPostBody);
        this.setState(newState);
        this.setState({
          name: name,
        })
      }else{
        return false;
      } 
    } else if(newPostBody.trim() !== "" && name.trim() !== ""){
      const newState = Object.assign({}, this.state);
      newState.posts.push(newPostBody);
      this.setState(newState);
      this.setState({
        name: name,
      })
    } else {
      return false;
    }
  }
 

  handleReplyForm = () =>{
    this.setState({
      replyForm: !this.state.replyForm
    })
  }


  likeUp = () => {
    this.setState({ likes: this.state.likes + 1 })
  }

  likeDown = () => {
    this.setState({ likes: this.state.likes - 1 })

  }

  render(){
    return(

      <div className="grid-container">
        <div className="Form">  
        <PostEditor addPost={this.addPost} />
        </div>
        <div className="Comments">
          {this.state.posts.map((postBody, idx) => {
            return (
              <Post
                id={idx}
                name={this.state.name}
                ref={this.post}
                replyFormId={idx}
                replyId={idx}
                key={idx}
                postBody={postBody}
                likeUp={this.likeUp}
                likeDown={this.likeDown}
                likes={this.state.likes}
                handleReplyForm={this.handleReplyForm}
                replyForm={this.state.replyForm}
                addPost={this.addPost}
                reply={this.state.reply}
                postId={this.state.postId}
              />
            )
        })}
        </div>
      </div>
    )
  }

}

export default ThreadDisplay;