import React, { Component } from 'react';
import './post-editor.css';
class PostEditor extends Component{
  state = {
    name:'',
    newPostBody:'',
    nameForm:'true',
  }

  handleNameInputChange = (event) => {
    this.setState({
      name: event.target.value
    })
  }

  handlePostEditorInputChange = (event) => {
    this.setState({
      newPostBody: event.target.value
    })
  }
  
  
  createPost = () => {
    this.props.addPost(this.state.newPostBody, this.state.name);
    this.setState({
      newPostBody:'',
    })
    if (this.state.name.trim() !== "" && this.state.newPostBody.trim() !== "" ){
      this.setState({
        nameForm:false,
      })
    } else if (this.state.name.trim() === "" && this.state.newPostBody.trim() === "") {
      alert('name,comment')
    } else if (this.state.name.trim() !== "" && this.state.newPostBody.trim() === ""){
      alert('comment')
    } else if (this.state.name.trim() === "" && this.state.newPostBody.trim() !== "") {
      alert('name')
    }
  
  }

  render(){
    return (
    <div>
      {
        this.state.nameForm 
        && 
        <div className="post-editor">
          <textarea placeholder="Write Your Name" value={this.state.name} onChange={this.handleNameInputChange} onSubmit={this.handleNameFormVisibility}/>
        </div>
      }
      <div className="post-editor">
          <textarea className="post-editor-input" placeholder="Write Your Comment" value={this.state.newPostBody} onChange={this.handlePostEditorInputChange} /> <br/>
          <button className="post-editor-button" onClick={this.createPost}>Post</button>
      </div>
    </div>
    )
  }

}

export default PostEditor;