import React,{Component} from 'react';
import './reply.css'
import ReplyForm from '../../reply-form/components/reply-form';

// Icons/
import upvoteIMG from '../../icons/images/upvote.png';
import downvoteIMG from '../../icons/images/downvote.png';
import replyIMG from "../../icons/images/reply-icon.png";

class Reply extends Component{

  state = {
    reply: [],
    likes: 0,
    replyForm: false,
  }

  handleReplyForm = () => {
    this.setState({
      replyForm: !this.state.replyForm
    })
  }
  likeUp = () => {
    this.setState({ likes: this.state.likes + 1 })
  }

  likeDown = () => {
    this.setState({ likes: this.state.likes - 1 })
    
  }

  addReply = (newReplyBody) => {
    if (newReplyBody.trim() !== "") {
      const newState = Object.assign({}, this.state);
      newState.reply.push(newReplyBody);
      this.setState(newState);
      if (this.state.replyForm) {
        this.setState({
          replyForm: false
        })
      }
    }
  }  

  render(){
    return(
    <div className="reply-border">
      <div className="reply">
        <div className="reply-name">
        {this.props.name}
        </div>
        <div className="reply-body">
          {this.props.replyBody}
        </div>
        <div className="reply-icons" >
          <img id="r-upvote" onClick={this.likeUp} src={upvoteIMG} alt="Upvote" width="25" />
          {this.state.likes}
          <img id="r-downvote" onClick={this.likeDown} src={downvoteIMG} alt="Downvote" width="25" />
          <img id="r-reply" onClick={this.handleReplyForm} src={replyIMG}alt="Reply" width="20" />
          <i onClick={this.handleReplyForm}>reply</i>
        </div>
      </div>
        <div>
          <ReplyForm
            handleReplyForm={this.handleReplyForm}
            replyForm={this.state.replyForm}
            addReply={this.addReply}
          />
        </div>
        {
          this.state.reply.map((replyBody, idx) => {
            return (
              <Reply
                name={this.props.name}
                key={idx}
                replyBody={replyBody}
                likeUp={this.likeUp}
                likeDown={this.likeDown}
                likes={this.state.likes}
                handleReplyForm={this.handleReplyForm}
                replyForm={this.state.replyForm}
              />
            )
          })}
      </div>

    )
  }
}


export default Reply;